pre, code {
    font-family: monospace;
}

pre {
    overflow: auto;
    -moz-tab-size:4;
      -o-tab-size:4;
         tab-size:4;
    border-radius: 0.5em;
    padding: 1.25em;
    max-width: 800px;
}

pre > code {
    padding: 1em;
    margin: 1em;
    word-wrap: normal;
}

.linenums li {
    list-style-type: decimal;
}

figcaption {
    font-size: 18px;
    font-weight: 300;
    color: #6d6d6d;
    padding: 1em;
    max-width: 800px;
}

.no-caption {
    display: none;
}


